header {
    height: 100vh;
    padding: 2rem;
    overflow: hidden;
}

.header__container {
    text-align: center;
    height: 100%;
    position: relative;
}

/* CTA */
.cta {
    margin-top: 2.5rem;
    display: flex;
    gap: 1rem;
    justify-content: center;
}

/* header social */
.header_social {
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 0.8rem;
    position: absolute;
    left: 0;
    bottom: 3rem;
}

.header_social::after {
    content: '';
    width: 1px;
    height: 2rem;
    background: var(--color-primary);
}

/* ME */
.me {
    background: linear-gradient(var(--color-primary), trasparent);
    width: 22rem;
    height: 30rem;
    position: absolute;
    left: calc(50% - 11rem);
    margin-top: 4rem;
    border-radius: 12rem 12 rem 0 0;
    overflow: hidden;
    padding: 5rem 1.5rem 1.5rem 1.5 rem;
}

.hero {
    border-radius: 50%;
}

/* scroll down */

.scroll_down {
    position: absolute;
    right: -2.3rem;
    bottom: 5rem;
    transform: rotate(90deg);
    font-weight: 300;
    font-size: 0.9rem;
}

/* tablets media queries MEDIUM SIZE */
@media screen and (max-width: 1024px){
    header {
        height: 68vh;
    }
    .hero {
        width:8rem;
        height: 8rem;
        justify-content: center;
        margin-left: 37%;
    }
 
}

/* tablets media queries small SIZE */
@media screen and (max-width: 600px){
    header {
        height: 100vh;
    }

    .header_social,
    .scroll_down {
        display: none;
    }
    .me {
        background: linear-gradient(var(--color-primary), trasparent);
        width: 22rem;
        height: 30rem;
        position: absolute;
        left: calc(50% - 11rem);
        margin-top: 4rem;
        border-radius: 12rem 12 rem 0 0;
        overflow: hidden;
        padding: 5rem 1.5rem 1.5rem 1.5 rem;
    }
}